@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

.container {
  max-width: 400px;
  background-color:rgba(1,1,1, 0.8);
  border-radius: 15px;
  overflow: hidden;
}
.Album {
  width: 100%;
}

.bgImage {
  /* Add the blur effect */
  filter: blur(20px);
  margin-left: -10px;

  /* Full width by default */
  width: 100%;
  height: auto; /* Adjust height automatically based on the aspect ratio */

  /* Center the background image */
  background-position: center;
  background-repeat: no-repeat;
  
  /* Set the initial background size */
  background-size: 100% auto; /* 100% width and auto height for larger screens */ 
}

/* Media query for devices with a max-width of 768px (common breakpoint for tablets and below) */
@media (max-width: 768px) {
  .bgImage {
    /* Set background size to auto 100% height for smaller screens */
    background-size: auto 100%; /* Auto width and 100% height for smaller screens */
    height: 100vh; /* Ensure the container is full-height on small screens */
    width: auto; /* Adjust width automatically */
  }
}

.songTitle {
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  font-style: normal;
}

.artistName {
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: normal;
}

.playBtn {
  font-size: 18px;
  color: #3f3;
}

.spotifyLogo {
  height: 35px;
  margin-left: 10;
}

.spotifyContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #121;
  padding: 20px;
}

.App {
  margin: 0;
  padding-top: 10px;
  text-align: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.main {
   margin: 0;
   padding: 0;
   overflow: hidden; 
 }

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
